import * as xlsx from 'xlsx';

function readXls(file){
  let wb=null;
  const reader=new FileReader();
  const rABS = !!reader.readAsBinaryString;
  if(rABS) reader.readAsArrayBuffer(file);else reader.readAsArrayBuffer(file);
  if(file){
    reader.onload=(e)=>{
      const bstr=e.target.result;
       wb=xlsx.read(bstr, {type:!rABS ? 'binary' : 'array'});
   }
   return new Promise((resovle,reject)=>{
     setTimeout(()=>{
       const data=xlsx.utils.sheet_to_json(wb.Sheets[wb.SheetNames[0]], {header:1});
       resovle(data||[]);
       reject([]);
     },1000)
  })
  }
  };

function writeXls(data){
  if(data&& Array.isArray(data)){
    const ws = xlsx.utils.json_to_sheet(data,{header:Object.keys(data[0])});
    const wb=xlsx.utils.book_new();
    xlsx.utils.book_append_sheet(wb,ws,"page0");
    xlsx.writeFile(wb,"SheetJS.xlsx");
  }
 
}
function uuid() {
  var s = [];
  var hexDigits = "0123456789abcdef";
  for (var i = 0; i < 36; i++) {
    s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
  }
  s[14] = "4"; // bits 12-15 of the time_hi_and_version field to 0010
  s[19] = hexDigits.substr((s[19] & 0x3) | 0x8, 1); // bits 6-7 of the clock_seq_hi_and_reserved to 01
  s[8] = s[13] = s[18] = s[23] = "-";
 
  var uuid = s.join("");
  return uuid;
}
export {
  uuid,
  readXls,
  writeXls
}